<template>
    <li @click="forwardTo(app.app_id)" :key="app.app_id" class="item-wrap clearfix">
      <div class="f-l"><img class="app-icon" :src="app.icon"/></div>
      <div class="f-l">
        <div class="app-name">{{app.app_name}}</div>
        <p class="app-desc">{{app.intro}}</p>
      </div>
      <div class="f-r"><a @click="downloadRecord(app.app_id, app.ct, $event, app.download_url)" href="javascript:;" class="download">下载</a></div>
    </li>
</template>
<style lang="scss" scoped>
  .f-l {
    float: left;
  }

  .f-r {
    float: right;
  }

  .clearfix::before,
  .clearfix::after {
    content: '';
    display: table;
  }

  .clearfix::after {
    clear: both;
  }

  .tabbar-wrap {
    width: 602px;
    height: 46px;
    padding: 14px 0 0;
    overflow-x: auto;
    overflow-y: hidden;
  }

  .item-bar {
    background: #FFFFFF;
    color: #B5B5B5;
    width: 86px;
    padding: 0 17px;
    height: 21px;
    line-height: 21px;
    margin-bottom: 11px;
    text-align: center;
  }

  .item-bar span.type {
    white-space: nowrap;
  }

  .item-bar.active span.type {
    color: #3a3a3a;
    border-bottom: 2px solid #3A3A3A;
  }
</style>
<script>
  export default {
    props: {
      app: {},
      downloadRecord: Function,
      forwardTo: Function
    },

    methods: {
    }
  }
</script>



