<!-- 分类细分详情 -->
<template>
  <div class="page type-details-page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#F8F8F8"
      menu-bg-color="#FDFDFD"
      color="#3A3A3A"
      :title="parentTitle"
      :show-back-button="true"
      :on-back-button-click="back"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content has-animate-header bg-light">
      <div ref="typeTabbar" class="tabbar-container">
        <ul :style="{width: tabLists.length * 86 + 'px'}" class="clearfix tabbar-wrap">
          <li v-for="(tab, index) in tabLists" :key="tab.category_id" @click="tabSlider($event,index,tab.category_id)" class="f-l item-bar" :class="{ active: activeIndex === index }"><span class="type">{{tab.title}}</span></li>
        </ul>
      </div>
      <div :style="{height: windowHeight + 'px'}" class="list-wrap">
        <scroller ref="appScroller" v-if="appLists.length > 0" height="100%" class="app-scroller" :onInfinite="infinite" noDataText="没有更多数据了" >
          <ul>
            <app-item v-for="app in appLists" :key="app.app_id" :app="app" :download-record="downloadRecord" :forward-to="forwardTo"></app-item>
          </ul>
        </scroller>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
  @import '../../../scss/mixins.scss';

  .type-details-page {
    font-size: 13px;

    @include font-regular();

    .bg-light {
      background: #FFF;
    }

    .tabbar-container {
      width: 100%;
      height: 46px;
      margin-top: 50px;
      overflow: hidden;
    }

    .f-l {
      float: left;
    }

    .f-r {
      float: right;
    }

    .clearfix::before,
    .clearfix::after {
      content: '';
      display: table;
    }

    .clearfix::after {
      clear: both;
    }

    .tabbar-wrap {
      width: 602px;
      height: 46px;
      padding: 14px 0 0;
      overflow-x: auto;
      overflow-y: hidden;
    }

    .item-bar {
      background: #FFFFFF;
      color: #B5B5B5;
      width: 86px;
      padding: 0 17px;
      height: 21px;
      line-height: 21px;
      margin-bottom: 11px;
      text-align: center;
    }

    .item-bar span.type {
      white-space: nowrap;
    }

    .item-bar.active span.type {
      color: #3a3a3a;
      border-bottom: 2px solid #3A3A3A;
    }

    .list-wrap {
      position: relative;
      height: 100%;

      .item-wrap {
        padding: 14px 16px;

        img.app-icon {
          width: 50px;
          height: 50px;
          padding: 2px;
          border-radius: 6px;
          position: relative;
        }

        div:nth-child(1).f-l {
          position: relative;
          width: 50px;
          height: 50px;
          margin-right: 10px;
        }

        div:nth-child(1).f-l::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid #dddddd;
          width: 100px;
          height: 100px;
          transform: scale(0.5, 0.5);
          box-sizing: border-box;
          transform-origin: 0 0;
          border-radius: 6px;
        }

        .app-name {
          font-size: 16px;
          color: #3A3A3A;
          line-height: 16px;
          margin: 5px 0 10px;
          width: 190px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .app-desc {
          font-size: 13px;
          color: #B5B5B5;
          line-height: 13px;
          max-width: 226px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .download {
          width: 48px;
          height: 26px;
          display: block;
          border: 1px solid #4a90e2;
          line-height: 26px;
          text-align: center;
          margin-top: 11px;
          text-decoration: none;
          border-radius: 2px;
        }
      }
    }

    g {
      stroke: #3a3a3a !important;
      fill: #3a3a3a !important;
    }
  }

  ._v-content {
    padding-bottom: 20px;
  }
</style>

<script>
  /* eslint eqeqeq: "off", camelcase: "off" */
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import AppItem from './AppItem.vue'
  import {hotapps_path} from 'constants'
  import {appendUrlParams} from 'utils'

  export default {
    components: {
      AnimateHeader,
      AppItem
    },

    data () {
      return {
        parent_id: 0,
        category_id: 0,
        last_id: 0,
        appLists: [],
        noMoreData: true,
        PAGE_SIZE: 20,
        activeIndex: 1,
        tabLists: [],
        parentTitle: '',
        windowHeight: window.innerHeight - 96
      }
    },

    mounted () {
      $bus.$on('scrollTotop', _ => {
        this.$refs.appScroller.scrollTo(0, 0, true)
      })
      let parent_id = this.$route.params.parent_id.split('_')[0]
      let category_id = this.$route.params.parent_id.split('_')[1]
      this.parent_id = parent_id
      this.category_id = category_id
      this.fetchData()

      let categoryLists = $storage.get('api:/s4/install.categoryList').res.data.payload
      let categoryItem = _.find(categoryLists, function (item) {
        return item.parent_id == parent_id
      })

      let tabLists = [{category_id: 0, title: '全部'}, ...categoryItem.list]
      let index = _.findIndex(tabLists, (item, i) => {
        return item.category_id == category_id
      })
      this.parentTitle = categoryItem.title
      this.tabLists = tabLists
      this.activeIndex = index
      this.$nextTick(() => {
        this.$refs.typeTabbar.scrollLeft = index * 86 / 2
        let startX = 0
        let $el_typeTabbar = this.$refs.typeTabbar
        this.$refs.typeTabbar.addEventListener('touchstart', function (event) {
          event.preventDefault()
          let x = event.touches[0].clientX
          // let y = event.touches[0].clientY
          startX = x
        }, false)
        this.$refs.typeTabbar.addEventListener('touchmove', function (event) {
          event.preventDefault()
          let x = event.touches[0].clientX
          // let y = event.touches[0].clientY
          let offset = startX - x
          if (offset < 0) {
            // 向右边滑动
            $el_typeTabbar.scrollLeft += offset
          } else {
            $el_typeTabbar.scrollLeft += offset
          }
          startX = x
        }, false)
        this.$refs.typeTabbar.addEventListener('touchend', function (event) {
          event.preventDefault()
          // let x = event.changedTouches[0].clientX
          // let y = event.changedTouches[0].clientY
        }, false)
      })
    },

    methods: {
      tabSlider (e, index, category_id) {
        $router.push('./' + this.parent_id + '_' + category_id)
        let offsetLeft = index * 86 / 2
        let clientX = e.clientX
        let start = this.$refs.typeTabbar.scrollLeft
        if (this.activeIndex === index) {
          return
        }
        let t = 0
        let callback = () => {
          let easeInSine = function (x, t, b, c, d) {
            return -c * (t /= d) * (t - 2) + b
          }
          t++
          start += easeInSine(0, t, 1, 15, 100)
          if (start <= offsetLeft) {
            this.$refs.typeTabbar.scrollLeft = start
            requestAnimationFrame(callback)
          }
        }
        let rightSliderCallback = () => {
          start -= 5
          if (start >= offsetLeft - 43) {
            this.$refs.typeTabbar.scrollLeft = start
            requestAnimationFrame(rightSliderCallback)
          }
        }
        if (clientX < 86 + 14) {
          requestAnimationFrame(rightSliderCallback)
        } else {
          requestAnimationFrame(callback)
        }

        this.activeIndex = index
        this.category_id = category_id
        this.last_id = 0
        this.noMoreData = true
        this.fetchData()
      },

      fetchData () {
        $loading.show('加载中...')
        this._list(this.parent_id, this.category_id, this.last_id).then((appLists) => {
          this.appLists = appLists
          $loading.hide()

          setTimeout(() => {
            this.noMoreData = this._numberLists(appLists) < this.PAGE_SIZE
          }, 100)
        })
      },

      infinite (done) {
        if (this.noMoreData) {
          done()
          return
        }

        this._list(this.parent_id, this.category_id, this.last_id).then(result => {
          setTimeout(() => {
            this.noMoreData = this._numberLists(result) < this.PAGE_SIZE

            if (this._numberLists(result) === 0) {
              done()
              return
            } else {
              this.appLists = this.appLists.concat(result)
              done()
            }
          }, 100)
        })
      },

      _list (parent_id, category_id, last_id) {
        return $api
          .installAppList({parent_id, category_id, last_id, limit: 20})
          .then(res => res.data && res.data.payload)
          .then(payload => {
            let appLists = payload
            let tmp

            _.each(appLists, (app, i) => {
              if (i === appLists.length - 1) {
                tmp = app.id
              }
            })
            this.last_id = tmp
            return appLists
          })
      },

      downloadRecord (app_id, ct, event, download_url) {
        event.stopPropagation()
        $api.installDownloadRecord({ app_id, ct })
          .then(res => res.data && res.data.payload)
          .then(payload => {
            location.href = appendUrlParams(download_url, {
              'return_url': location.href
            })
          })
      },

      forwardTo (appId) {
        let fromPath = this.$route.path
        let _path = '/hotapps/appDetail/' + appId
        $storage.set(hotapps_path, fromPath)
        $router.forward(_path)
      },

      _numberLists (lists) {
        return lists.length
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/hotapps/classify')
        $router.back({ path })
      }
    }
  }
</script>



